import React, { Component } from 'react';
import './Card.css';

class CardTeacher extends Component {
    render() {
        const { name, link, image } = this.props.data;

        return (
            // eslint-disable-next-line
            <a herf={link}>
                <img src={image} alt="" className="card__avatar" />
                <span>{name}</span>
            </a>
        );
    }
}

export default CardTeacher;